import { ChangeDetectionStrategy, Component, Input, OnInit, } from '@angular/core';
import { FormControl, FormGroup, ValidatorFn, Validators } from '@angular/forms';
//
import { TranslateService } from '@ngx-translate/core';
//
import { BaseReactiveFormComponent } from '../../../../../ui/components/base-reactive-form/base-reactive-form-component';
import { Brand } from '../../../ms-brands/models/brand';
import { Shop } from '../../../ms-shops/models/shops';
import { Category } from '../../../ms-categories/models/category';
import { Style } from '../../models/style';

import { NewBrandComponent } from '../../../ms-brands/components/new-brand/new-brand.component';
import { ShopsSellingStylecomponentModalComponent } from '../../../ms-shops/components/shops-selling-style-modal/shops-selling-style-modal.component';
import { MatDialog, MatDialogRef } from '@angular/material';

@Component({
    selector: 'style-form',
    templateUrl: './style-form.component.html',
    styleUrls: ['./style-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class StyleFormComponent extends BaseReactiveFormComponent<Style> implements OnInit {

    @Input() brands: Array<Brand>;

    @Input() categories: Array<Category>;

    @Input() styles: Array<Style>;

    modalRef: MatDialogRef<ShopsSellingStylecomponentModalComponent | NewBrandComponent>;

    shop: Shop;

    constructor(
        public dialog: MatDialog,
        translateService: TranslateService) {
        super(translateService);
        //setTranslations(this.translateService, TRANSLATIONS);
    }

    ngOnInit() {
        let validationsErrors: any[] = [
            {
                type: 'required',
                key: 'Required Field',
                params: null,
                translation: ''
            }
        ];

        this.validationErrorMessages = validationsErrors;

        this.createFormGroup();
    }

    createFormGroup() {

        this.formGroup = new FormGroup({
            name: new FormControl(this.data.name, [Validators.required]),
            description: new FormControl(this.data.description),
            brand: new FormControl(this.data.brand, [Validators.required]),
            parent: new FormControl(this.data.parent),
            category: new FormControl(this.data.category, [Validators.required]),
            //updatedAt: new FormControl(this.data.updatedAt)            
        });

    }

    submitClicked() {
        if (this.formGroup.valid) {
            this.accept.emit(this.data);
        } else {
            this.triggerValidation();
        }
    }

    showModalStoresSelling() {
        this.modalRef = this.dialog.open(ShopsSellingStylecomponentModalComponent, {
            height: '90%',
            width: '90%',
            data: { face: this.shop }
        });
    }

    showModalAddNewBrand() {
        this.modalRef = this.dialog.open(NewBrandComponent, {
            height: '90%',
            width: '90%',
            data: { face: this.shop }
        });
    }
}

